@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;600;700&display=swap");
@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: "Source Sans Pro", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1 {
  font-family: "Source Serif Pro", serif;
  font-weight: 700;
}

.districtShape:hover {
  fill: rgb(67,182,161,.85);
}

/* .legend {
  width: 440px;
  height: 300px;
  left: -104px;
  margin-right: calc( 100% - 440px);
  overflow: visible;
} */

.legend1 {
  top: 3.3rem;
  left: -4rem;
}

.legend2 {
  top: 6rem;
  left: -5.3rem;
}

.legend3 {
  top: 9rem;
  left: -5.3rem;
}

.legend4 {
  top: 4.5rem;
  left: 10.5rem;
}

.card {
  width: 220px;
  height: 300px;
  border-radius: 15px;
  overflow: visible;
}

.card {
  box-shadow: -2px 2px 4px rgb(202,202,205,0.5);
}

.numberStudents {
  transform: translate(0, -5.5rem);
}

.studentsLabel {
  transform: translate(0, -4rem);
}

@media only screen and (max-width: 767px) {
  .definitions {
    display: none;
  }

  .donuts {
    justify-content: center;
  }

  .card {
    width: 190px;
  }

  .legend {
    left: -120px;
  }
}

